import {
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpErrorResponse
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError, of } from 'rxjs';
import { catchError, switchMap } from 'rxjs/operators';
import { AuthService } from './auth.service';
import { Router } from '@angular/router';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {

  
  constructor(private authService: AuthService, private router: Router) {
    console.log('TokenInterceptor instantiated');
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    console.log('TokenInterceptor engaged:', req.url); // <-- Check if this appears
    // Your interceptor logic

    // Skip auth header for login or auth endpoints
    if (req.url.includes('/auth')) {
      return next.handle(req);
    }

    const token = this.authService.getToken();
    const authReq = token
      ? req.clone({ setHeaders: { Authorization: `Bearer ${token}` } })
      : req;

    return next.handle(authReq).pipe(
      catchError((error: HttpErrorResponse) => {
        console.log(error);
        if (error.status === 401) {
          // Attempt silent re-auth
          return this.authService.initAuth().pipe(
            switchMap((success: boolean) => {
              if (success) {
                const newToken = this.authService.getToken();
                const retryReq = req.clone({
                  setHeaders: {
                    Authorization: `Bearer ${newToken}`
                  }
                });
                return next.handle(retryReq);
              } else {
                this.authService.logout();
                this.router.navigate(['/login']);
                return throwError(() => new Error('Authentication failed'));
              }
            }),
            catchError(() => {
              this.authService.logout();
              this.router.navigate(['/login']);
              return throwError(() => new Error('Authentication failed'));
            })
          );
        } else if (error.status === 403) {
          // Access Denied
          this.authService.logout();
          this.router.navigate(['/login']);
          return throwError(() => new Error('Access denied: Insufficient permissions'));
        }

        // Other errors
        return throwError(() => error);
      })
    );
  }
}
